@import url('./fonts.css');

/* css variables */

:root {
    --white: #fff;
    --black: #0F0F14;
    --light-text: #ffffffcc;
    --dark-text: #000;
    --light-border: #e5e5e6;
    --dark-border: #333;
    --light-gray: #f6f6f8;
    --dark-gray: #f5f5f70a;
    --light-blue: #009fed;
    --dark-blue: #4c6dee;
    --text: #000000cc;
}

/* main styles */

* {
    box-sizing: border-box;
    font-family: 'graphik';
    margin: 0;
    padding: 0;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}

body {
    overflow-x: hidden;
    min-width: 320px;
    position: relative;
}

@media (max-width: 320px) {
    body {
        overflow-x: auto;
    }
}

button, input {
    border: none;
    outline: none;
    background: none;
}

button {
    cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus {
  -webkit-text-fill-color: var(--black);
  transition: background-color 5000s ease-in-out 0s;
}

/* styles */

.wrapper {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    background-color: var(--white);
}

.wrapper-dark {
    background-color: var(--black);
}

.container {
    width: 1440px;
    min-width: 320px;
    padding: 0 70px;
}

/* header */

.header {
    width: 100%;
    height: 55px;
    display: flex;
    justify-content: space-between;
    padding: 6px 0;
}

.logo {
    display: flex;
    align-items: center;
}

.logo svg {
    margin-right: 7px;
}

.logo-title {
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    color: #332d54;
}

.logo-title-dark {
    color: #f5f4fa;
}

.menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}   

.nav {
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    color: var(--text);
    text-decoration: none;
    cursor: pointer;
    transition: .15s linear;
}

.nav:hover {
    color: #e049f9;
}

.nav-dark {
    color: var(--light-text);
}

.nav-dark:hover {
    color: #caa6ff;
}

.btn-mode {
    margin: 0 19px;
}

.btn-mode svg path {
    transition: .15s linear;
}

.btn-mode:hover  svg path {
    fill: #e049f9;
}

.btn-mode-dark:hover svg path {
    fill: #caa6ff;
}

.btn-menu {
    height: 43px;
    border-radius: 8px;
    background-image: linear-gradient(var(--white), var(--white)),  linear-gradient(to right, #FF6073 3.48%, #FF41BE 61.23%, #C52DFA 118.98%);
    text-decoration: none;
    border: 1px solid transparent;
    background-origin: border-box;
    background-clip: content-box, border-box;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-menu span {
    width: 100%;
    border-radius: 8px;
    padding: 0 19px;
    background-image: linear-gradient(to right, #FF6073 3.48%, #FF41BE 61.23%, #C52DFA 118.98%);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
}

.btn-menu-dark {
    background-image: linear-gradient(var(--black), var(--black)),  linear-gradient(to right, #FD7182 0.04%, #FB7DFF 50%, #BA8EFB 99.96%);
}

.btn-menu-dark span {
    background-image: linear-gradient(to right, #FD7182 0.04%, #FB7DFF 50%, #BA8EFB 99.96%);
}

/* website short info styles */

.title-wrapper {
    width: 100%;
    margin: 28px 0 30px;
    background: var(--light-gray);
    border-radius: 20px;
}

.title-wrapper-dark {
    background: var(--dark-gray);
}

.title-box {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 34px 250px;
    border-radius: 20px;
    background-image: url(../icons/title-bg.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.title {
    font-weight: bold;
    font-size: 36px;
    line-height: 40px;
    color: var(--black);
    text-align: center;
}

.title-dark {
    color: var(--white);
}

.description {
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    color: var(--text);
    text-align: center;
    padding: 13px 0 25px;
}

.description-dark {
    color: var(--light-text);
}

.title-box-btn {
    background-image: linear-gradient(to right, #FF6073 3.48%, #FF41BE 61.23%, #C52DFA 118.98%);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    cursor: pointer;
    text-decoration: none;
    position: relative;
}

.title-box-btn::after {
    content: '';
    width: 100%;
    height: 1px;
    background-image: linear-gradient(to right, #FF6073 3.48%, #FF41BE 61.23%, #C52DFA 118.98%);
    position: absolute;
    bottom: -1px;
    left: 0;
}

.title-box-btn-dark {
    background-image: linear-gradient(to right, #FD7182 0.04%, #FB7DFF 50%, #BA8EFB 99.96%);
}

.title-box-btn-dark::after {
    background-image: linear-gradient(to right, #FD7182 0.04%, #FB7DFF 50%, #BA8EFB 99.96%);
}

/* Sales list styles */

.list-box-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 20px;
    color: var(--black);
    margin-bottom: 11px;
}

.list-box-title-dark {
    color: var(--white);
}

.items {
    width: 100%;
}

.item {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
    padding: 4px;
    border-radius: 11px;
    border: 1px solid var(--light-border);
}

.item-dark {
    border: 1px solid var(--dark-border);
}

.photos {
    display: flex;
    min-width: fit-content;
}

.photo {
    width: 168px;
    height: 168px;
    border-radius: 8px;
    margin-right: 4px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.item-main {
    width: 100%;
    padding: 9px 26px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.item-header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.item-title {
    font-weight: bold;
    font-size: 18px;
    line-height: 20px;
    color: var(--black);
    padding-right: 20px;
}

.item-title-dark {
    color: var(--white);
}

.item-menu {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
}

.item-btn {
    height: 26px;
    border-radius: 5px;
    background-image: linear-gradient(var(--white), var(--white)), linear-gradient(to right, #FF6073 3.48%, #FF41BE 61.23%, #C52DFA 118.98%);
    text-decoration: none;
    border: 1px solid transparent;
    background-origin: border-box;
    background-clip: content-box, border-box;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.item-btn-dark {
    background-image: linear-gradient(var(--black), var(--black)),  linear-gradient(to right, #FD7182 0.04%, #FB7DFF 50%, #BA8EFB 99.96%);
}

.item-btn span {
    width: 100%;
    border-radius: 8px;
    padding: 0 8.5px 0 12px;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: var(--black);
    transition: .15s linear;
}

.item-btn svg {
    margin-left: 4.5px;
}

.item-btn-dark span {
    color: var(--white);
}

.item-btn-dark svg path {
    stroke: var(--white);
    transition: .15s linear;
}

.item-btn:hover span {
    background-image: linear-gradient(to right, #FF6073 3.48%, #FF41BE 61.23%, #C52DFA 118.98%);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
}

.item-btn-dark:hover span {
    background-image: linear-gradient(to right, #FD7182 0.04%, #FB7DFF 50%, #BA8EFB 99.96%);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
}

.item-btn:hover svg path {
    stroke: #db35e5;
}

.item-btn-dark:hover svg path {
    stroke: #bd8dfb;
}

.item-description {
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    color: var(--text);
    padding: 10px 0;
}

.item-description-dark {
    color: var(--light-text);
}

.item-icon {
    margin-left: 17px;
}

.item-info {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.item-info-col {
    width: 50%;
    display: flex;
    flex-direction: column;
}

.item-info-col:last-child {
    min-width: 380px;
}

.item-info-text {
    font-weight: normal;
    font-size: 15px;
    line-height: 17px;
    color: var(--black);
    margin-top: 7px;
}

.item-info-text-dark {
    color: var(--white);
}

.item-info-text span {
    font-weight: bold;
    color: var(--light-blue);
    margin-right: 25px;
}

.item-info-text-row {
    display: flex;
    align-items: center;
}

.item-loader-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px 0 24px;
}

.item-loader {
    width: 56px;
    height: 56px;
    border: 6px solid #f6f6f8;
    border-radius: 50%;
    border-top: 6px solid #009fed;
    -webkit-animation: spin 1.5s linear infinite;
    animation: spin 1.5s linear infinite;
}

.item-loader-dark {
    border: 6px solid rgba(245, 245, 247, 0.04);
    border-top: 6px solid #009fed;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* styles for touch screen device */

@media (pointer: coarse) {
    .nav:hover {
        color: var(--text);
    }
    .nav-dark:hover {
        color: var(--light-text);
    }
    .btn-mode:hover svg path {
        fill: var(--black);
    }
    .btn-mode-dark:hover svg path {
        fill: var(--white);
    }
    .item-btn:hover span, .item-btn-dark:hover span {
        background-image: none;
        -webkit-background-clip: none;
        -moz-background-clip: none;
        -webkit-text-fill-color: inherit;
        -moz-text-fill-color: inherit;
    }    
    .item-btn:hover svg path {
        stroke: var(--black);
    }
    .item-btn-dark:hover svg path {
        stroke: var(--white);
    }
}

/* Media queries */

@media (max-width: 1440px) {
    .container {
        width: 1160px;
        padding: 0 20px;
    }
    .title-box {
        background-size: contain;
        background-position: top;
        padding: 34px 96px;
    }
    .photo {
        width: 144px;
        height: 144px;
    }
    .item-main {
        padding: 6px 18px;
    }
    .item-title {
        font-size: 16px;
        line-height: 18px;
        padding-right: 10px;
    }
    .item-btn {
        height: 24px;
        border-radius: 4px;
    }
    .item-btn span {
        border-radius: 4px;
        padding: 0 6px 0 10px;
        font-size: 13px;
        line-height: 15px;
    }
    .item-btn svg {
        margin-left: 3px;
    }
    .item-description {
        font-size: 14px;
        line-height: 16px;
        padding: 8px 0;
    }
    .item-icon {
        margin-left: 10px;
    }
    .item-info-col:last-child {
        min-width: 360px;
    }
    .item-info-text {
        font-size: 14px;
        line-height: 16px;
        margin-top: 5px;
    }
    .item-info-text span {
        margin-right: 15px;
    }
}

@media (max-width: 1180px) {
    .container {
        width: 1024px;
    }
    .item-info-text span {
        display: block;
    }
    .item-info-col:last-child {
        min-width: 320px;
    }
}

@media (max-width: 1024px) {
    .container {
        width: 100%;
    }
    .nav {
        font-size: 14px;
        line-height: 16px;
    }
    .btn-mode {
        margin: 0 14px;
    }
    .btn-menu {
        height: 36px;
        border-radius: 6px;
    }
    .btn-menu span {
        border-radius: 6px;
        padding: 0 12px;
        font-size: 14px;
        line-height: 16px;
    }
    .title-wrapper {
        margin: 24px 0 26px;
        border-radius: 18px;
    }
    .title-box {
        padding: 34px;
    }
    .title {
        font-size: 28px;
        line-height: 32px;
    }
    .description {
        font-size: 14px;
        line-height: 16px;
        padding: 10px 0 22px;
    }
    .title-box-btn {
        font-size: 15px;
        line-height: 17px;
    }
    .list-box-title {
        font-size: 16px;
        line-height: 18px;
        margin-bottom: 8px;
    }
    .item {
        justify-content: flex-start;
        flex-direction: column;
    }
    .photos {
        min-width: auto;
        width: 100%;
    }
    .item-main {
        padding: 10px 6px;
    }
    .item-info-col:last-child {
        min-width: 360px;
    }
    .item-info-text span {
        display: inline;
    }
    .item-loader-box {
        padding: 12px 0 20px;
    }
    .item-loader {
        width: 48px;
        height: 48px;
        border: 5px solid #f6f6f8;
        border-top: 5px solid #009fed;
    }
    .item-loader-dark {
        border: 5px solid rgba(245, 245, 247, 0.04);
        border-top: 5px solid #009fed;
    }
}

@media (max-width: 680px) {
    .title-box {
        padding: 25px;
        background-image: none;
    }
    .title {
        font-size: 24px;
        line-height: 28px;
    }
    .photos {
        justify-content: space-between;
    }
    .photo {
        width: calc((100vw - 48px) / 3);
        height: calc((100vw - 48px) / 3);
    }
    .photo:last-child {
        margin-right: 0;
    }
    .item-header {
        flex-direction: column-reverse;
        align-items: flex-end;
    }
    .item-title {
        width: 100%;
        padding-right: 0;
        padding-top: 6px;
    }
    .item-info {
        flex-direction: column-reverse;
        align-items: flex-start;
    }
    .item-info-col {
        width: 100%;
    }
    .item-info-col:last-child {
        min-width: auto;
        padding-bottom: 6px;
    }
}

@media (max-width: 440px) {
    .container {
        padding: 0 12px;
    }
    .header {
        height: 48px;
    }
    .logo svg {
        width: 36px;
        margin-right: 4px;
    }
    .logo-title {
        font-size: 14px;
        line-height: 16px;
    }
    .nav {
        font-size: 13px;
        line-height: 15px;
    }
    .btn-mode {
        margin: 0 10px;
    }
    .btn-menu {
        height: 34px;
        max-width: 80px;
    }
    .btn-menu span {
        padding: 0 10px;
        font-size: 13px;
        line-height: 15px;
    }
}
